$header-height: 1000px;
$bg-color: rgb(38, 38, 38);
$font-color: #c6c6c6;
$title-size: 60px;
$button-size: 15px;
$overview-size: 25px;
$misc-size: 17px;
$title-padding-left: 100px;
$button-padding: 7px 15px 7px 15px;
$header-padding: 60px $title-padding-left 20px $title-padding-left;
$tagline-padding: 40px $title-padding-left 40px $title-padding-left;
$tagline-font-size: 40px;
$navigator-padding: 30px 20px 30px 20px;
$main-red: rgb(167, 71, 71);
$main-red-active: rgb(113, 54, 54);
$transition-duration: 0.4s;

body {
    background-color: $bg-color;
}

.DetailView {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: $bg-color;

    &Header {
        display: flex;
        color: white;
        box-shadow: inset 0px -130px 100px 100px $bg-color;
        width: 100%;
        height: $header-height;
        background-size: cover;
        background-position: top;
        justify-content: flex-end;
        flex-direction: column;
        padding: $header-padding;
        transition: $transition-duration;

        &TitleWrapper {
            display: flex;
            flex-direction: row;
            align-items: baseline;
            height: 80%;
            overflow: auto;
            
            h1 {
                max-width: 75%;
                margin: 0;
                align-self: flex-end;
                font-size: $title-size;
            }

            p {
                margin-left: 40px;
                font-style: italic;
                align-self: flex-end;
                font-size: $overview-size;
            }
        }

        &ButtonWrapper {
            display: flex;
            flex-direction: row;
            margin-top: 20px;

            a {
                font-size: $button-size;
                color: white;
                border-radius: 100px;
                padding: $button-padding;
                background-color: $main-red;
                margin-left: 10px;
                margin-right: 10px;
            }
        }
    }

    &Tagline {
        padding: $tagline-padding;
        width: 100%; 
        margin: 0;
        color: white;
        text-align: left;

        h1 {
            font-family: Merriweather;;
            font-size: $tagline-font-size; 
        }
    }

    &ContentWrapper {
        display: flex;
        flex-direction: row;
        width: 80%;
        margin-bottom: 150px;

        &MiscWrapper {
            width: 25%;
            display: flex;
            flex-direction: column;
            color: $font-color;

            &Component {
                display: flex;
                flex-direction: row;
                justify-content: flex-start;
                align-items: center;
                margin: 3px;

                &FontAwesome {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    text-align: center;
                    width: 20%;
                    height: 40px;
                    color: $font-color;
				    margin: 10px;
                } 

                p {
                    width: 80%;
                    font-size: $misc-size;
                    vertical-align: inherit;
                }
            }


        }

        &Overview {
            width: 75%;
            display: flex;
            justify-content: center;
            align-items: baseline;
            color: $font-color;
            font-size: $overview-size;

            p {
                margin: 0px;
            }
        }
    }

    &Navigator {
        display: flex;
        position: fixed;
        bottom: 0%;
        flex-direction: row;
        width: 100%;
        justify-content: space-between;
        align-items: center;
        color: white;
        font-size: $tagline-font-size;
        background-color: $bg-color;
        z-index: 99999;
        box-shadow: -10px -10px 40px $bg-color;

        div {
            display: flex;
            width: 50%;
            height: 100%;
            align-content: center;
            justify-content: center;
            transition: $transition-duration;
            padding: $navigator-padding;
            
            &:hover {
                cursor: pointer;
                background-color: $main-red;
                transition: $transition-duration;
            }

            &:active {
                background-color: $main-red-active;
            }
        }
    }
}